<template>
  <div class="d-flex flex-column vh-100">
    <div class="page py-4" style="overflow-x:hidden;">
      <div class="d-flex justify-content-between ml-3 mb-3 pl-3 pr-2-5">
        <div class="header flex-column">
          <span class="header-text">Order Received</span>
          <span class="header-subtext">This view will be shown in messenger.</span>
        </div>
        <div class="count">{{count}}</div>
      </div>
      <router-link to="/account/manageorders">
        <BackButton />
      </router-link>

      <div class="d-flex align-items-center pt-5 ml-3 mb-3 px-3">
        <span class="head-icon">
          <SearchIcon class="icon" />
        </span>
        <input type="text" placeholder="Search here" v-model="search" class="form-control" />
      </div>

      <div class="Customer" v-for="user in orderList" :key="user.user.user.ID">
        <div
          :class="show==user.user.user.ID ? 'd-flex justify-content-between bg-gray' : 'd-flex justify-content-between'"
          @click="showItem(user.user.user.ID)"
        >
          <div class="d-flex align-items-center px-3 py-2">
            <div class="d-flex align-items-center Customer-item ml-3">
              <div class="d-flex flex-column align-items-center">
                <div class="circle-image mr-3" :style="image"></div>
                <span @click.stop="hideItem" class="arrow-up mr-3" v-if="show==user.user.user.ID">
                  <ArrowUpIcon class="arrow" />
                </span>
              </div>
              <div class="d-flex flex-column mr-3">
                <span class="Customer--header">{{user.user.user.Username}}</span>
                <div class="Customer--subheader">
                  <span class="pt-1">{{user.user.user.Phone}}</span>
                  <span class="py-1">{{user.user.user.Address}}</span>
                  <span class="Customer--time">15 mins ago</span>
                </div>
              </div>
            </div>
          </div>
          <div
            class="d-flex flex-column justify-content-center align-items-center py-2 px-1-0 icon-container"
            @click.stop="confirmedOrder(user.user.voucherCode,user.user.user.UserAccountID)"
          >
            <span>
              <CorrectIcon class="icon" />
            </span>
            <span class="icon--subheader">Confirmed</span>
          </div>
          <div
            class="d-flex flex-column justify-content-center align-items-center py-2 px-2-0 icon-container"
          >
            <span>
              <PhoneRingIcon class="icon" />
            </span>
            <span class="icon--subheader">Call</span>
          </div>
        </div>

        <div class="Product d-flex flex-column bgWhiteGray" v-if="show==user.user.user.ID">
          <div
            class="Product-item ml-3 px-3 py-3"
            v-for="order in user.orderDetail"
            :key="order.ID"
          >
            <div class="d-flex align-items-center">
              <div class="Product-image mr-3" :style="image"></div>
              <div class="d-flex flex-column mr-3">
                <span class="Product--header">{{order.ItemName}}</span>
                <div class="Product--subheader">
                  <span class="pt-1">{{order.SalePrice}} MMK</span>
                  <span class="Product--quantity">{{order.ItemQty}} Qtys</span>
                </div>
              </div>
            </div>
            <div class="d-flex">
              <!--<div class="Product--subheader align-items-center mr-4">
                <CorrectIcon class="icon" />
                <span class="py-2">Confirmed</span>
              </div>-->
              <div class="Product--subheader align-items-center mr-4">
                <MenuIcon class="icon" />
                <span class="py-2">Menu</span>
              </div>
            </div>
          </div>
          <!-- <div class="Product-item ml-3 px-3 py-3">
            <div class="d-flex align-items-center">
              <div class="Product-image mr-3" :style="image"></div>
              <div class="d-flex flex-column mr-3">
                <span class="Product--header">Galaxy S10</span>
                <div class="Product--subheader">
                  <span class="pt-1">300,000 MMK</span>
                  <span class="Product--quantity">4 Qtys</span>
                </div>
              </div>
            </div>
            <div class="d-flex">
              <div class="Product--subheader align-items-center mr-4">
                <CorrectIcon class="icon" />
                <span class="py-2">Confirmed</span>
              </div>
              <div class="Product--subheader align-items-center mr-4">
                <MenuIcon class="icon" />
                <span class="py-2">Menu</span>
              </div>
            </div>
          </div>-->
        </div>
      </div>
    </div>
    <Loading v-if="showLoading" />
    <bottom-navigation-bar />
  </div>
</template>

<script>
import BottomNavigationBar from "../../../components/BottomNavigationBar";
import SearchIcon from "@/assets/icons/Search";
import PhoneRingIcon from "@/assets/icons/PhoneRing";
import ArrowUpIcon from "@/assets/icons/ArrowUp";
import MenuIcon from "@/assets/icons/Menu";
import CorrectIcon from "@/assets/icons/Correct";
import BackButton from "@/components/BackButton";
import Loading from "@/components/Loading";
import axios from "axios";

export default {
  components: {
    BottomNavigationBar,
    SearchIcon,
    PhoneRingIcon,
    ArrowUpIcon,
    MenuIcon,
    CorrectIcon,
    BackButton,
    Loading
  },
  data() {
    return {
      search: "",
      showLoading: true,
      show: 0,
      status: "ORDERED",
      order: [],
      image: { backgroundImage: "url(https://via.placeholder.com/150)" }
    };
  },
  methods: {
    showItem(id) {
      this.show = id;
      //console.log(this.show);
    },
    hideItem() {
      this.show = 0;
    },
    async getOrder() {
      var res = await axios.get(
        `order/list?shopID=${this.shop.id}&orderStatus=${this.status}&name=${this.search}`
      );
      if (res.data != null) {
        this.order = res.data;
        // console.log(this.orderList);
      }
      this.showLoading = false;
    },
    async confirmedOrder(code, id) {
      var res = await axios.get(
        `order/confirmedorder?voucherCode=${code}&userAccID=${id}&orderStatus=${this.status}&shopID=${this.shop.id}`
      );
      if (res.status == 200) {
        let index = this.order.findIndex(
          x =>
            x.user.voucherCode == res.data.VoucherCode &&
            x.user.user.UserAccountID == res.data.UserAccountID
        );
        this.order.splice(index, 1);
        this.$store.commit("updateReceivedCount");
      }
    }
  },
  watch: {
    search() {
      this.getOrder();
    }
  },
  computed: {
    count() {
      return this.$store.state.received;
    },
    orderList() {
      return this.order;
    },
    shop() {
      return this.$store.state.shop;
    }
  },
  mounted() {
    this.getOrder();
  }
};
</script>

<style lang="scss">
@import "@/scss/_PageSetup.scss";
@import "@/scss/_Utilities.scss";
</style>

<style lang="scss" scoped>
@import "@/scss/_variables";

.page {
  flex: 1;
  overflow-y: auto;
}
.header {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
  margin-bottom: 1rem;
  &-text {
    font-size: 1.1rem;
    font-weight: bold;
  }
  &-subtext {
    font-size: 0.8rem;
  }
}
.count {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 1000;
  display: flex;
}
.head-icon {
  font-size: 1.4rem;
  color: $textColor;
  cursor: pointer;
}
.icon {
  width: 1.2rem;
  height: 1.2rem;
  cursor: pointer;
}
.form-control {
  font-size: 0.9rem;
  border: none;
  &:focus {
    box-shadow: none;
  }
}
.Customer {
  color: $textColor;
  &-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: none !important;
  }

  &--header {
    font-size: 1rem;
    font-weight: bold;
  }
  &--subheader {
    font-size: 0.7rem;
    line-height: 1rem;
    display: flex;
    flex-direction: column;
  }
  &--arrow {
    font-size: 1.5rem;
    font-weight: bold;
    display: flex;
    flex-direction: row;
  }
  &--time {
    color: $primaryActive;
    font-size: 0.6rem;
  }
}
.circle-image {
  border-radius: 50%;
  min-width: 4rem;
  min-height: 4rem;
  background-size: cover;
  background-position: center;
}
.px-1-0 {
  padding-left: 1rem !important;
  padding-right: 1rem !important;
}
.px-2-0 {
  padding-left: 2rem !important;
  padding-right: 2rem !important;
}
.pr-2-5 {
  padding-right: 2.5rem !important;
}
.Product {
  &-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: none !important;
  }
  &-image {
    border-radius: 50%;
    min-width: 4rem;
    min-height: 4rem;
    background-size: cover;
    background-position: center;
    border-radius: 1rem;
  }
  &--header {
    font-size: 1rem;
    font-weight: bold;
  }
  &--subheader {
    font-size: 0.7rem;
    line-height: 1rem;
    display: flex;
    flex-direction: column;
  }
  &--quantity {
    color: $primaryActive;
    font-size: 0.6rem;
  }
}
.arrow-up {
  margin-top: -1rem;
}
.arrow {
  width: 1.5rem;
  height: 1.5rem;
}
.bgWhiteGray {
  background: #f7f7f7 !important;
}
.icon-container {
  &:focus {
    background: $lightPurple !important;
  }
  &:hover {
    background: $lightPurple !important;
  }
}
.icon--subheader {
  font-size: 0.7rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
}
</style>